@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

nav {
  background: transparent;
  width: 100%;
  display: flex;
  justify-content: center; /* Distribute items evenly */
  padding: 0.3rem 0;
  z-index: 2;
  position: fixed;
  top: 0;
  backdrop-filter: blur(5px);
  transition: background-color 0.3s;
}

nav.scrolled {
  background-color: rgba(2, 1, 1, 0.8); /* Use rgba to set a transparent grey color */
}



nav a {
  text-decoration: none; /* Removes the underline from links */
  font-family: 'Roboto', sans-serif;

  background: transparent; 
  padding: 0.9rem;
  border-radius: 50%;
  display: flex;
  color: var(--color-light);
  font-size: 1.1rem;
  opacity: 1; /* Add opacity property to hide the text initially */
  transition: opacity 0.3s; /* Add a transition for smooth opacity change */
} 

nav.scrolled a {
  opacity: 1; /* Make the text appear when the navbar is scrolled */
}

/* nav a:hover {
  background: rgba(0, 0, 0, 0.3);
} */

nav a.active {
  background: var(--color-bg);
  color: var(--color-white);
}
